// Open modal window - 
// document.querySelector('.order-modal').classList.remove('hidden')
import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

const modal = document.querySelector('.order-modal');
const form = document.querySelector('.order-form');
const closeBtn = document.querySelector('.order-close');

let pageTitle = '';
if (modal && !modal.classList.contains('hidden')) {
	pageTitle = document.title;
	document.title = "–ó–∞–º–æ–≤–∏—Ç–∏ –∑–≤–æ—Ä–æ—Ç–Ω–∏–π –∑–≤‚Äô—è–∑–æ–∫.";
	document.body.style.overflow = 'hidden';
}

closeBtn.addEventListener('click', () => closeModal());
modal.addEventListener('click', e => {
	if (e.target === modal) closeModal();
});
document.addEventListener('keydown', e => {
	if (e.key === 'Escape') closeModal();
});

function closeModal() {
	modal.classList.add('hidden');
	document.title = pageTitle;
	document.body.style.overflow = '';
	if ('selectedFurnitureId' in localStorage) {
		localStorage.removeItem('selectedFurnitureId');
	};
	if ('selectedFurnitureColor' in localStorage) {
		localStorage.removeItem('selectedFurnitureColor');
	};
}

form.addEventListener('submit', async e => {
	e.preventDefault();

	const nameInput = form.elements.name;
	const phoneInput = form.elements.phone;
	const commentInput = form.elements.comment;

	let valid = true;
	[nameInput, phoneInput, commentInput].forEach(input => {
		input.classList.remove('error');
		if (!input.value.trim()) {
			input.classList.add('error');
			valid = false;
		}
	});

	const phonePattern = /^\+?\d{10,15}$/;
	if (!phonePattern.test(phoneInput.value.trim())) {
		phoneInput.classList.add('error');
		iziToast.error({
			title: '–ü–æ–º–∏–ª–∫–∞',
			message: '–í–≤–µ–¥—ñ—Ç—å –ø—Ä–∞–≤–∏–ª—å–Ω–∏–π –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω—É —É —Ñ–æ—Ä–º–∞—Ç—ñ +380XXXXXXXXX',
			position: 'center',
			timeout: 3000,
		});
		return;
	}

	const comment = commentInput.value.trim();

	const commentPattern = /^[a-zA-Z–∞-—è–ê-–Ø—ó–á—ñ–Ü—î–Ñ“ë“ê0-9\s.,!?'"()\-:;]{5,300}$/u;

	if (!commentPattern.test(comment)) {
		commentInput.classList.add('error');
		iziToast.error({
			title: '–ü–æ–º–∏–ª–∫–∞',
			message: '–ö–æ–º–µ–Ω—Ç–∞—Ä –º–∞—î –º—ñ—Å—Ç–∏—Ç–∏ –≤—ñ–¥ 5 –¥–æ 300 —Å–∏–º–≤–æ–ª—ñ–≤ —ñ –Ω–µ –º—ñ—Å—Ç–∏—Ç–∏ –∑–∞–±–æ—Ä–æ–Ω–µ–Ω–∏—Ö –∑–Ω–∞–∫—ñ–≤.',
			position: 'center',
			timeout: 3000,
		});
		return;
	}

	if (!valid) {
		iziToast.error({
			title: '–ü–æ–º–∏–ª–∫–∞',
			message: '–ó–∞–ø–æ–≤–Ω—ñ—Ç—å —É—Å—ñ –æ–±–æ–≤ º—è–∑–∫–æ–≤—ñ –ø–æ–ª—è!',
			position: 'center',
			timeout: 3000,
		});
		return;
	}

	const demo_product_id = '682f9bbf8acbdf505592ac36';
	const demo_color = '#1212ca';

	const payload = {
		name: nameInput.value.trim(),
		phone: phoneInput.value.trim(),
		modelId: localStorage.getItem('selectedFurnitureId') || demo_product_id,
		color: localStorage.getItem('selectedFurnitureColor') || demo_color,
		comment: comment,
	};

	try {
		const response = await fetch('https://furniture-store-v2.b.goit.study/api/orders', {
			method: 'POST',
			headers: { 'Content-Type': 'application/json' },
			body: JSON.stringify(payload),
		});

		if (!response.ok) {
			const errorData = await response.json();
			iziToast.error({
				title: '–ü–æ–º–∏–ª–∫–∞ —Å–µ—Ä–≤–µ—Ä–∞',
				message: errorData.message || '–ù–µ –≤–¥–∞–ª–æ—Å—è –∑—Ä–æ–±–∏—Ç–∏ –∑–∞–º–æ–≤–ª–µ–Ω–Ω—è.',
				position: 'center',
				timeout: 3000,
			});
			return;
		}

		iziToast.success({
			title: '–£—Å–ø—ñ—à–Ω–æ!',
			message: '–í–∞—à—É –∑–∞—è–≤–∫—É –≤—ñ–¥–ø—Ä–∞–≤–ª–µ–Ω–æ.',
			position: 'center',
			timeout: 3000,
		});

		form.reset();
		closeModal();

	} catch (error) {
		iziToast.error({
			title: '–ü–æ–º–∏–ª–∫–∞',
			message: error.message || '–ù–µ –≤–¥–∞–ª–æ—Å—è –≤—ñ–¥–ø—Ä–∞–≤–∏—Ç–∏ –∑–∞—è–≤–∫—É',
			position: 'topRight',
		});
	}
});